import React from 'react';
import { motion } from 'framer-motion';
import { Users, Database, FileText, Scale, RotateCw, Link, Search } from 'lucide-react';
import { Link as RouterLink } from 'react-router-dom';
import Navbar from '../components/Navbar';

const AboutUsPage = () => {
  const fadeIn = {
    initial: { opacity: 0, y: 20 },
    animate: { opacity: 1, y: 0 },
    transition: { duration: 0.6 }
  };

  const stats = [
    { label: '', value: '500+' },
    { label: '', value: '1000+' },
    { label: '', value: '5000+' },
    { label: '', value: '100+' }
  ];

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-900 via-gray-800 to-gray-900 text-white overflow-hidden">
      <Navbar />
      
      {/* Hero Section */}
      <section className="relative py-20 px-4 flex flex-col items-center justify-center text-center">
        <motion.h1 
          className="text-5xl font-bold mb-6"
          initial={{ opacity: 0, y: -30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
        >
          
        </motion.h1>
        <motion.p 
          className="text-xl mb-8 max-w-2xl"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ delay: 0.2, duration: 0.5 }}
        >
          
          positive change.
        </motion.p>
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.4, duration: 0.5 }}
        >
          <RouterLink 
            to="/advanced-search" 
            className="bg-blue-500 hover:bg-blue-600 text-white font-bold py-3 px-6 rounded-full transition duration-300 inline-flex items-center"
          >
            <Search className="mr-2" />
            
          </RouterLink>
        </motion.div>
      </section>

      <div className="container mx-auto px-4 py-16">
        {/* Mission Statement */}
        <motion.section {...fadeIn} className="mb-20">
          <h2 className="text-4xl font-semibold mb-6 text-center"></h2>
          <p className="text-xl text-gray-300 text-center max-w-3xl mx-auto">
            
            
          </p>
        </motion.section>

        {/* Key Stats */}
        <motion.section {...fadeIn} className="mb-20">
          <div className="grid grid-cols-2 md:grid-cols-4 gap-8">
            {stats.map((stat, index) => (
              <motion.div
                key={index}
                className="bg-gray-800 rounded-lg p-6 text-center"
                whileHover={{ scale: 1.05 }}
                transition={{ type: "spring", stiffness: 300 }}
              >
                <h3 className="text-4xl font-bold text-blue-400 mb-2">{stat.value}</h3>
                <p className="text-gray-400">{stat.label}</p>
              </motion.div>
            ))}
          </div>
        </motion.section>

        {/* Our Work */}
        <motion.section {...fadeIn} className="mb-20">
          <h2 className="text-4xl font-semibold mb-10 text-center"></h2>
          <div className="grid md:grid-cols-2 gap-8">
            <WorkCard
              icon={<Database className="w-10 h-10 text-blue-400" />}
              title=""
              description=""
            />
            <WorkCard
              icon={<FileText className="w-10 h-10 text-green-400" />}
              title=""
              description=""
            />
            <WorkCard
              icon={<Scale className="w-10 h-10 text-yellow-400" />}
              title=""
              description=""
            />
            <WorkCard
              icon={<Link className="w-10 h-10 text-purple-400" />}
              title=""
              description=""
            />
          </div>
        </motion.section>

        {/* Team */}
        <motion.section {...fadeIn} className="mb-20">
          <h2 className="text-4xl font-semibold mb-10 text-center"></h2>
          <div className="grid md:grid-cols-3 gap-8">
            <TeamMember name="" role="" />
            <TeamMember name="" role="" />
            <TeamMember name="" role="" />
          </div>
        </motion.section>

        {/* Call to Action */}
        <motion.section {...fadeIn} className="text-center">
          <h2 className="text-4xl font-semibold mb-6"></h2>
          <p className="text-xl text-gray-300 mb-8">
            
          </p>
          <button className="bg-blue-500 hover:bg-blue-600 text-white font-bold py-3 px-6 rounded-full transition duration-300">
            
          </button>
        </motion.section>
      </div>
    </div>
  );
};

const WorkCard = ({ icon, title, description }) => (
  <motion.div
    className="bg-gray-800 rounded-lg p-6"
    whileHover={{ scale: 1.03 }}
    transition={{ type: "spring", stiffness: 300 }}
  >
    <div className="flex items-center mb-4">
      {icon}
      <h3 className="text-2xl font-semibold ml-4">{title}</h3>
    </div>
    <p className="text-gray-300">{description}</p>
  </motion.div>
);

const TeamMember = ({ name, role }) => (
  <motion.div
    className="bg-gray-800 rounded-lg p-6 text-center"
    whileHover={{ scale: 1.05 }}
    transition={{ type: "spring", stiffness: 300 }}
  >
    <div className="w-24 h-24 bg-gray-600 rounded-full mx-auto mb-4"></div>
    <h3 className="text-xl font-semibold mb-2">{name}</h3>
    <p className="text-gray-400">{role}</p>
  </motion.div>
);

export default AboutUsPage;