import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import { Calendar, Users, AlertTriangle, Check, X, Book, ArrowLeft } from 'lucide-react';
import { motion } from 'framer-motion';
import Navbar from '../components/Navbar';
import LoadingSpinner from '../components/LoadingSpinner';
import ErrorMessage from '../components/ErrorMessage';

const HumanRightsImpactLevel = ({ level }) => {
  const getColor = () => {
    switch (level) {
      case 'Low': return 'bg-green-500';
      case 'Medium': return 'bg-yellow-500';
      case 'High': return 'bg-red-500';
      default: return 'bg-gray-500';
    }
  };

  return (
    <span className={`px-3 py-1 ${getColor()} text-white text-sm rounded-full`}>
      {level}
    </span>
  );
};

const ConventionReference = ({ reference }) => (
  <motion.div
    initial={{ opacity: 0, y: 20 }}
    animate={{ opacity: 1, y: 0 }}
    transition={{ duration: 0.5 }}
    className="bg-gray-700 rounded-lg p-3 mt-2"
  >
    <h5 className="font-semibold text-blue-300 mb-1">{reference.name}</h5>
    <p className="text-sm text-gray-300">{reference.article}</p>
    <p className="text-xs text-gray-400 mt-1">{reference.content}</p>
  </motion.div>
);

const MeetingDetailPage = () => {
  const { id } = useParams();
  const [meeting, setMeeting] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const mockMeetingDetail =  {
    id: 1,
    title: "",
    date: "2024-10-01T10:00:00",
    participants: 25,
    type: "",
    description: "",
    decisions: [
      {
        title: "",
        content: "",
        humanRightsImpact: "High",
        impactAssessment: [
          { positive: true, description: "" },
          { positive: true, description: "" },
          { positive: false, description: "" }
        ],
        recommendedAction: "",
        conventions: [
          { name: "", article: "9", content: "" },
          { name: "", article: "13", content: "" }
        ]
      },
      {
        title: "",
        content: "",
        humanRightsImpact: "Medium",
        impactAssessment: [
          { positive: true, description: "" },
          { positive: true, description: "" },
          { positive: false, description: "" }
        ],
        recommendedAction: "",
        conventions: [
          { name: "", article: "19", content: "" }
        ]
      },
      {
        title: "",
        content: "",
        humanRightsImpact: "High",
        impactAssessment: [
          { positive: true, description: "" },
          { positive: true, description: "" },
          { positive: false, description: "" }
        ],
        recommendedAction: "",
        conventions: [
          { name: "", article: "10", content: "" }
        ]
      }
    ]
  };
  
  useEffect(() => {
    //  API 
    const fetchData = () => {
      setTimeout(() => {
        try {
          setMeeting(mockMeetingDetail);
          setLoading(false);
        } catch (err) {
          setError("");
          setLoading(false);
        }
      }, 1000);
    };

    fetchData();
  }, [id]);

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-900 text-white">
        <Navbar />
        <div className="container mx-auto px-4 py-8">
          <LoadingSpinner />
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen bg-gray-900 text-white">
        <Navbar />
        <div className="container mx-auto px-4 py-8">
          <ErrorMessage message={error} />
        </div>
      </div>
    );
  }

  if (!meeting) {
    return (
      <div className="min-h-screen bg-gray-900 text-white">
        <Navbar />
        <div className="container mx-auto px-4 py-8">
          <h1 className="text-4xl font-bold mb-8"></h1>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-900 via-gray-800 to-gray-900 text-white">
      <Navbar />
      <div className="container mx-auto px-4 py-8">
        <motion.div
          initial={{ opacity: 0, y: -50 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
        >
          <button
            onClick={() => window.history.back()}
            className="mb-4 flex items-center text-blue-400 hover:text-blue-300 transition-colors duration-200"
          >
            <ArrowLeft className="w-5 h-5 mr-2" />
            
          </button>
          <h1 className="text-4xl font-bold mb-8">{meeting.title}</h1>
        </motion.div>
        
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5, delay: 0.2 }}
          className="bg-gray-800 rounded-xl p-6 shadow-lg mb-8"
        >
          <div className="flex items-center text-gray-400 mb-4">
            <Calendar className="w-5 h-5 mr-2" />
            <span>{new Date(meeting.date).toLocaleDateString()}</span>
          </div>
          <div className="flex items-center text-gray-400 mb-4">
            <Users className="w-5 h-5 mr-2" />
            <span>{meeting.participants} </span>
          </div>
          <p className="text-lg mb-4">{meeting.description}</p>
          <div className="flex items-center">
            <span className="mr-2">:</span>
            <span className="px-3 py-1 bg-blue-500 text-white text-sm rounded-full">
              {meeting.type}
            </span>
          </div>
        </motion.div>

        <motion.h2
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5, delay: 0.4 }}
          className="text-2xl font-semibold mb-4"
        >
          
        </motion.h2>
        {meeting.decisions && meeting.decisions.length > 0 ? (
          meeting.decisions.map((decision, index) => (
            <motion.div
              key={index}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5, delay: 0.2 * (index + 3) }}
              className="bg-gray-800 rounded-xl p-6 shadow-lg mb-6"
            >
              <h3 className="text-xl font-semibold mb-3">{decision.title}</h3>
              <p className="mb-4">{decision.content}</p>
              <div className="flex items-center mb-2">
                <AlertTriangle className="w-5 h-5 mr-2 text-yellow-400" />
                <span className="mr-2">:</span>
                <HumanRightsImpactLevel level={decision.humanRightsImpact} />
              </div>
              {decision.impactAssessment && decision.impactAssessment.length > 0 && (
                <div className="mt-4">
                  <h4 className="font-semibold mb-2">:</h4>
                  <ul className="list-disc list-inside space-y-2">
                    {decision.impactAssessment.map((item, i) => (
                      <li key={i} className="flex items-start">
                        {item.positive ? (
                          <Check className="w-5 h-5 mr-2 text-green-400 flex-shrink-0 mt-1" />
                        ) : (
                          <X className="w-5 h-5 mr-2 text-red-400 flex-shrink-0 mt-1" />
                        )}
                        <span>{item.description}</span>
                      </li>
                    ))}
                  </ul>
                </div>
              )}
              {decision.recommendedAction && (
                <div className="mt-4">
                  <h4 className="font-semibold mb-2">:</h4>
                  <p>{decision.recommendedAction}</p>
                </div>
              )}
              {decision.conventions && decision.conventions.length > 0 && (
                <div className="mt-4">
                  <h4 className="font-semibold mb-2 flex items-center">
                    <Book className="w-5 h-5 mr-2 text-blue-400" />
                    :
                  </h4>
                  {decision.conventions.map((convention, i) => (
                    <ConventionReference key={i} reference={convention} />
                  ))}
                </div>
              )}
            </motion.div>
          ))
        ) : (
          <p className="text-gray-400"></p>
        )}
      </div>
    </div>
  );
};

export default MeetingDetailPage;
