import React, { useState, useEffect } from 'react';
import { useParams, Link } from 'react-router-dom';
import { motion, AnimatePresence } from 'framer-motion';
import { ArrowLeft, Clock, User, FileText, Book, Scale, ChevronDown, ChevronUp } from 'lucide-react';
import Navbar from '../components/Navbar';

const LawDetailPage = () => {
  const { id } = useParams();
  const [law, setLaw] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [activeTab, setActiveTab] = useState('content');
  const [expandedChapters, setExpandedChapters] = useState({});

  useEffect(() => {
    fetchLawDetails();
  }, [id]);

  const fetchLawDetails = async () => {
    setLoading(true);
    try {
      //  API 
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      // 
      const lawData = {
        id: 'su1',
        title: '',
        category: '',
        lastUpdated: '2024-03-15',
        level: '',
        department: '',
        legislativeHistory: [
          { date: '2020-09-01', event: '' },
          { date: '2022-05-15', event: ' 3712 ' },
          { date: '2024-03-15', event: ' 15-1  20 ' }
        ],
        content: [
          {
            chapter: ' ',
            articles: [
              { number: '', content: '' },
              { number: '', content: '' },
              { number: '', content: '\n\n\n\n\n' }
            ]
          },
          {
            chapter: ' ',
            articles: [
              { number: '', content: '' },
              { number: '', content: '\n\n\n\n' },
              { number: '', content: '\n\n\n' }
            ]
          }
        ]
      };

      setLaw(lawData);
      // 
      const initialExpandedState = {};
      lawData.content.forEach((chapter, index) => {
        initialExpandedState[index] = true;
      });
      setExpandedChapters(initialExpandedState);
    } catch (error) {
      console.error("Error fetching law details:", error);
      setError("");
    } finally {
      setLoading(false);
    }
  };

  const toggleChapter = (index) => {
    setExpandedChapters(prev => ({ ...prev, [index]: !prev[index] }));
  };

  const getLevelIcon = (level) => {
    switch (level) {
      case '':
        return <Scale className="text-yellow-400" size={24} />;
      case '':
        return <Book className="text-blue-400" size={24} />;
      case '':
        return <FileText className="text-green-400" size={24} />;
      default:
        return <FileText className="text-gray-400" size={24} />;
    }
  };

  const renderContent = () => {
    if (loading) {
      return (
        <div className="flex items-center justify-center h-[calc(100vh-64px)]">
          <div className="animate-spin rounded-full h-32 w-32 border-t-2 border-b-2 border-blue-500"></div>
        </div>
      );
    }

    if (error) {
      return (
        <div className="container mx-auto px-4 py-8">
          <div className="bg-red-100 border-l-4 border-red-500 text-red-700 p-4 rounded" role="alert">
            <p className="font-bold"></p>
            <p>{error}</p>
          </div>
        </div>
      );
    }

    if (!law) {
      return null;
    }

    return (
      <div className="container mx-auto px-4 py-8">
        <Link to="/laws" className="flex items-center text-blue-500 hover:text-blue-600 mb-6">
          <ArrowLeft size={20} className="mr-2" />
          
        </Link>

        <div className="bg-gray-800 rounded-lg shadow-lg p-6 mb-8">
          <h1 className="text-3xl font-bold text-white mb-4">{law.title}</h1>
          <div className="flex flex-wrap items-center gap-4 mb-4">
            <div className="flex items-center text-gray-300">
              <Clock size={18} className="mr-2" />
              {law.lastUpdated}
            </div>
            <div className="flex items-center text-gray-300">
              <User size={18} className="mr-2" />
              {law.department}
            </div>
            <div className="flex items-center text-gray-300">
              {getLevelIcon(law.level)}
              <span className="ml-2">{law.level}</span>
            </div>
          </div>
        </div>

        <div className="mb-8">
          <div className="flex border-b border-gray-700">
            <button
              className={`py-2 px-4 ${activeTab === 'content' ? 'text-blue-500 border-b-2 border-blue-500' : 'text-gray-400'}`}
              onClick={() => setActiveTab('content')}
            >
              
            </button>
            <button
              className={`py-2 px-4 ${activeTab === 'history' ? 'text-blue-500 border-b-2 border-blue-500' : 'text-gray-400'}`}
              onClick={() => setActiveTab('history')}
            >
              
            </button>
          </div>
        </div>

        {activeTab === 'content' && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{ duration: 0.5 }}
          >
            {law.content.map((chapter, chapterIndex) => (
              <div key={chapterIndex} className="mb-6 bg-gray-800 rounded-lg shadow-lg overflow-hidden">
                <button
                  onClick={() => toggleChapter(chapterIndex)}
                  className="w-full px-6 py-4 flex justify-between items-center bg-gray-700 hover:bg-gray-600 transition-colors duration-300"
                >
                  <h2 className="text-xl font-semibold text-white">{chapter.chapter}</h2>
                  {expandedChapters[chapterIndex] ? <ChevronUp size={20} className="text-gray-400" /> : <ChevronDown size={20} className="text-gray-400" />}
                </button>
                <AnimatePresence>
                  {expandedChapters[chapterIndex] && (
                    <motion.div
                      initial={{ opacity: 0, height: 0 }}
                      animate={{ opacity: 1, height: 'auto' }}
                      exit={{ opacity: 0, height: 0 }}
                      transition={{ duration: 0.3 }}
                    >
                      {chapter.articles.map((article, articleIndex) => (
                        <div key={articleIndex} className="px-6 py-4 border-t border-gray-700">
                          <h3 className="text-lg font-medium text-blue-400 mb-2">{article.number}</h3>
                          <p className="text-gray-300 whitespace-pre-line">{article.content}</p>
                        </div>
                      ))}
                    </motion.div>
                  )}
                </AnimatePresence>
              </div>
            ))}
          </motion.div>
        )}

        {activeTab === 'history' && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{ duration: 0.5 }}
            className="relative"
          >
            <div className="absolute left-0 w-0.5 h-full bg-blue-500"></div>
            {law.legislativeHistory.map((event, index) => (
              <div key={index} className="relative pl-8 pb-8">
                <div className="absolute left-0 w-4 h-4 bg-blue-500 rounded-full -ml-2 mt-1"></div>
                <div className="bg-gray-800 rounded-lg shadow-lg p-4">
                  <div className="flex items-center mb-2 text-blue-400">
                    <Clock size={16} className="mr-2" />
                    <span>{event.date}</span>
                  </div>
                  <p className="text-gray-300">{event.event}</p>
                </div>
              </div>
            ))}
          </motion.div>
        )}
      </div>
    );
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-900 to-gray-800 text-white">
      <Navbar />
      {renderContent()}
    </div>
  );
};

export default LawDetailPage;