import React from 'react';
import { HelpCircle, ChevronRight, Bell } from 'lucide-react';
import { motion } from 'framer-motion';

const NewcomerGuide = () => (
  <motion.div
    initial={{ opacity: 0, y: 20 }}
    animate={{ opacity: 1, y: 0 }}
    transition={{ duration: 0.5, delay: 0.6 }}
    className="mt-16 bg-gray-800 rounded-lg p-6 shadow-lg"
  >
    <h3 className="text-2xl font-bold mb-6 flex items-center">
      <HelpCircle className="mr-2" />
      
    </h3>
    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
      <div>
        <p className="mb-4"></p>
        <ul className="space-y-3">
          <li className="flex items-center">
            <ChevronRight className="mr-2 text-blue-400" />
            
          </li>
          <li className="flex items-center">
            <ChevronRight className="mr-2 text-blue-400" />
            
          </li>
          <li className="flex items-center">
            <ChevronRight className="mr-2 text-blue-400" />
            
          </li>
          <li className="flex items-center">
            <ChevronRight className="mr-2 text-blue-400" />
            SDGs
          </li>
        </ul>
      </div>
      <div className="bg-gray-700 p-4 rounded-lg">
        <h4 className="text-lg font-semibold mb-3 flex items-center">
          <Bell className="mr-2 text-yellow-400" />
          
        </h4>
        <p className="mb-3"></p>
        <p className="mb-3">2024920</p>
        <button className="bg-blue-500 hover:bg-blue-600 text-white px-4 py-2 rounded-full transition duration-300">
          
        </button>
      </div>
    </div>
  </motion.div>
);

export default NewcomerGuide;
