import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Search, Filter, UserPlus, ChevronDown, ChevronUp, Phone, Mail,  X } from 'lucide-react';

const StudentUnionPersonnelManagement = () => {
  const [personnel, setPersonnel] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedDepartment, setSelectedDepartment] = useState('All');
  const [expandedMember, setExpandedMember] = useState(null);
  const [currentTerm, setCurrentTerm] = useState(null);
  const [showAddModal, setShowAddModal] = useState(false);
  const [newMember, setNewMember] = useState({
    name: '',
    department: '',
    position: '',
    email: '',
    phone: '',
    startDate: '',
    endDate: ''
  });
  const [adminDepartments, setAdminDepartments] = useState(['', '', '']);
  const [newAdminDepartment, setNewAdminDepartment] = useState('');
  const [departmentError, setDepartmentError] = useState('');

  const departments = [
    'All',
    '',
    '',
    '',
    '',
    '',
    ...adminDepartments.map(dept => ` - ${dept}`)
  ];

  useEffect(() => {
    fetchPersonnel();
    fetchCurrentTerm();
  }, []);

  const fetchPersonnel = async () => {
    setLoading(true);
    try {
      //  API 
      await new Promise(resolve => setTimeout(resolve, 1000));

      const personnelData = [
        // 
        {
          _id: '1',
          name: '',
          department: '',
          position: '',
          contact: {
            email: 'daming.wang@example.com',
            phone: '0912345678'
          },
          term: {
            start: new Date('2023-09-01'),
            end: new Date('2025-08-31')
          },
          avatar: '/api/placeholder/32/32'
        },
        {
          _id: '2',
          name: '',
          department: ' - ',
          position: '',
          contact: {
            email: 'xiaohua.li@example.com',
            phone: '0923456789'
          },
          term: {
            start: new Date('2024-03-01'),
            end: new Date('2025-02-28'),
            number: 15
          },
          avatar: '/api/placeholder/32/32'
        }
      ];

      setPersonnel(personnelData);
    } catch (error) {
      console.error("Error fetching personnel:", error);
      setError("");
    } finally {
      setLoading(false);
    }
  };

  const fetchCurrentTerm = async () => {
    try {
      //  API 
      await new Promise(resolve => setTimeout(resolve, 500));
      setCurrentTerm(15);
    } catch (error) {
      console.error("Error fetching current term:", error);
    }
  };

  const filteredPersonnel = personnel.filter(person =>
    (person.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
     person.position.toLowerCase().includes(searchTerm.toLowerCase())) &&
    (selectedDepartment === 'All' || person.department === selectedDepartment)
  );

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setNewMember(prev => ({ ...prev, [name]: value }));
  };

  const validateNewMember = () => {
    const startDate = new Date(newMember.startDate);
    const endDate = new Date(newMember.endDate);
    const duration = endDate - startDate;

    if (newMember.department.startsWith('')) {
      if (duration !== 2 * 365 * 24 * 60 * 60 * 1000) {
        return '';
      }
    } else if (newMember.department.startsWith('')) {
      if (duration !== 365 * 24 * 60 * 60 * 1000) {
        return '';
      }
      // 
      if (!['', '', ''].includes(newMember.position)) {
        return '';
      }
    } else if (newMember.department === '') {
      if (duration !== 2 * 365 * 24 * 60 * 60 * 1000) {
        return '';
      }
    } else if (newMember.department === '') {
      if (!['', '', '', ''].includes(newMember.position)) {
        return '';
      }
    }

    return null; //  null
  };

  const handleAddMember = () => {
    const error = validateNewMember();
    if (error) {
      alert(error);
      return;
    }

    const newPersonnel = {
      _id: Date.now().toString(), //  ID
      name: newMember.name,
      department: newMember.department,
      position: newMember.position,
      contact: {
        email: newMember.email,
        phone: newMember.phone
      },
      term: {
        start: new Date(newMember.startDate),
        end: new Date(newMember.endDate),
        number: newMember.department.startsWith('') ? currentTerm : undefined
      },
      avatar: '/api/placeholder/32/32' // 
    };

    setPersonnel(prev => [...prev, newPersonnel]);
    setShowAddModal(false);
    setNewMember({
      name: '',
      department: '',
      position: '',
      email: '',
      phone: '',
      startDate: '',
      endDate: ''
    });
  };

  const handleAddAdminDepartment = async () => {
    setDepartmentError('');
    if (!newAdminDepartment) {
      setDepartmentError('');
      return;
    }
    if (adminDepartments.includes(newAdminDepartment)) {
      setDepartmentError('');
      return;
    }

    // 
    try {
      await new Promise(resolve => setTimeout(resolve, 500)); // 
      setAdminDepartments(prev => [...prev, newAdminDepartment]);
      setNewAdminDepartment('');
      alert(` "${newAdminDepartment}" `);
    } catch (error) {
      console.error("Error adding department:", error);
      setDepartmentError("");
    }
  };

  const PersonnelCard = ({ person }) => (
    <motion.div
      layout
      initial={{ opacity: 0, y: 50 }}
      animate={{ opacity: 1, y: 0 }}
      exit={{ opacity: 0, y: -50 }}
      className="bg-gradient-to-br from-gray-800 to-gray-900 rounded-lg shadow-lg overflow-hidden mb-4 p-4"
    >
      <div className="flex items-start justify-between">
        <div>
          <h3 className="text-xl font-semibold text-white mb-2">{person.name} - {person.position}</h3>
          <p className="text-gray-400 text-sm mb-2">
            {person.department}
            {person.department.startsWith('') && person.term.number && ` ( ${person.term.number} )`}
          </p>
          <p className="text-gray-300 mb-2">
            {person.term.start.toLocaleDateString()} - {person.term.end.toLocaleDateString()}
          </p>
        </div>
        <img src={person.avatar} alt={person.name} className="w-12 h-12 rounded-full" />
      </div>
      <button
        onClick={() => setExpandedMember(expandedMember === person._id ? null : person._id)}
        className="inline-flex items-center mt-2 px-4 py-2 bg-gray-700 text-gray-200 rounded-full hover:bg-gray-600 transition-colors duration-300"
      >
        {expandedMember === person._id ? '' : ''}
        {expandedMember === person._id ? <ChevronUp size={16} className="ml-2" /> : <ChevronDown size={16} className="ml-2" />}
      </button>
      {expandedMember === person._id && (
        <div className="mt-4 space-y-2">
          <div className="flex items-center text-gray-300">
            <Mail size={16} className="mr-2" />
            {person.contact.email}
          </div>
          <div className="flex items-center text-gray-300">
            <Phone size={16} className="mr-2" />
            {person.contact.phone}
          </div>
        </div>
      )}
    </motion.div>
  );

  const AddMemberModal = () => (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
      <div className="bg-gray-800 p-6 rounded-lg w-96">
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-xl font-bold text-white"></h2>
          <button onClick={() => setShowAddModal(false)} className="text-gray-400 hover:text-white">
            <X size={24} />
          </button>
        </div>
        <input
          type="text"
          name="name"
          placeholder=""
          value={newMember.name}
          onChange={handleInputChange}
          className="w-full p-2 mb-2 bg-gray-700 text-white rounded"
        />
        <select
          name="department"
          value={newMember.department}
          onChange={handleInputChange}
          className="w-full p-2 mb-2 bg-gray-700 text-white rounded"
        >
          <option value=""></option>
          {departments.map(dept => (
            <option key={dept} value={dept}>{dept}</option>
          ))}
        </select>
        <input
          type="text"
          name="position"
          placeholder=""
          value={newMember.position}
          onChange={handleInputChange}
          className="w-full p-2 mb-2 bg-gray-700 text-white rounded"
        />
        <input
          type="email"
          name="email"
          placeholder=""
          value={newMember.email}
          onChange={handleInputChange}
          className="w-full p-2 mb-2 bg-gray-700 text-white rounded"
        />
        <input
          type="tel"
          name="phone"
          placeholder=""
          value={newMember.phone}
          onChange={handleInputChange}
          className="w-full p-2 mb-2 bg-gray-700 text-white rounded"
        />
        <input
          type="date"
          name="startDate"
          value={newMember.startDate}
          onChange={handleInputChange}
          className="w-full p-2 mb-2 bg-gray-700 text-white rounded"
        />
        <input
          type="date"
          name="endDate"
          value={newMember.endDate}
          onChange={handleInputChange}
          className="w-full p-2 mb-2 bg-gray-700 text-white rounded"
        />
        <button
          onClick={handleAddMember}
          className="w-full p-2 bg-blue-600 text-white rounded hover:bg-blue-500"
        >
          
        </button>
      </div>
    </div>
  );

  const AddDepartmentModal = () => (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
      <div className="bg-gray-800 p-6 rounded-lg w-96">
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-xl font-bold text-white"></h2>
          <button onClick={() => setNewAdminDepartment('')} className="text-gray-400 hover:text-white">
            <X size={24} />
          </button>
        </div>
        <input
          type="text"
          value={newAdminDepartment}
          onChange={(e) => setNewAdminDepartment(e.target.value)}
          placeholder=""
          className="w-full p-2 mb-2 bg-gray-700 text-white rounded"
        />
        {departmentError && <p className="text-red-500 text-sm mb-2">{departmentError}</p>}
        <button
          onClick={handleAddAdminDepartment}
          className="w-full p-2 bg-blue-600 text-white rounded hover:bg-blue-500"
        >
          
        </button>
      </div>
    </div>
  );

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-900 to-gray-800 text-white">
      <div className="container mx-auto px-4 py-8">
        <div className="flex flex-col md:flex-row justify-between items-center mb-8">
          <h1 className="text-4xl font-bold mb-4 md:mb-0"></h1>
          <div className="flex items-center space-x-4">
            <div className="relative">
              <input
                type="text"
                placeholder="..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="w-full md:w-64 p-2 pl-10 pr-4 rounded-full bg-gray-700 text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-blue-400"
              />
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" size={18} />
            </div>
            <div className="relative inline-block text-left">
              <select
                value={selectedDepartment}
                onChange={(e) => setSelectedDepartment(e.target.value)}
                className="block appearance-none w-full bg-gray-700 border border-gray-600 text-white py-2 px-4 pr-8 rounded-full leading-tight focus:outline-none focus:bg-gray-600 focus:border-gray-500"
              >
                <option value="All"></option>
                {departments.map(dept => (
                  <option key={dept} value={dept}>{dept}</option>
                ))}
              </select>
              <div className="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-white">
                <Filter size={18} />
              </div>
            </div>
          </div>
        </div>

        <AnimatePresence>
          {loading ? (
            <motion.div
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              exit={{ opacity: 0 }}
              className="flex items-center justify-center h-64"
            >
              <div className="animate-spin rounded-full h-32 w-32 border-t-2 border-b-2 border-blue-500"></div>
            </motion.div>
          ) : error ? (
            <motion.div
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              exit={{ opacity: 0 }}
              className="bg-red-600 text-white p-4 rounded-lg"
            >
              {error}
            </motion.div>
          ) : (
            filteredPersonnel.map(person => (
              <PersonnelCard key={person._id} person={person} />
            ))
          )}
        </AnimatePresence>

        {filteredPersonnel.length === 0 && !loading && !error && (
          <div className="text-center py-12">
            <p className="text-2xl text-gray-400"></p>
            <p className="text-gray-500 mt-2"></p>
          </div>
        )}

        <div className="mt-8 flex justify-end">
          <button
            onClick={() => setShowAddModal(true)}
            className="inline-flex items-center px-4 py-2 bg-blue-600 text-white rounded-full hover:bg-blue-500 transition-colors duration-300"
          >
            <UserPlus size={18} className="mr-2" />
            
          </button>
          <button
            onClick={() => handleAddAdminDepartment()}
            className="inline-flex items-center px-4 py-2 bg-green-600 text-white rounded-full hover:bg-green-500 transition-colors duration-300 ml-4"
          >
            
          </button>
        </div>

        {showAddModal && <AddMemberModal />}
        {newAdminDepartment && <AddDepartmentModal />}
      </div>
    </div>
  );
};

export default StudentUnionPersonnelManagement;
