import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import {  Filter, Loader, Calendar, Users, ChevronRight } from 'lucide-react';
import { Link } from 'react-router-dom';
import Navbar from '../components/Navbar';
import SearchBar from '../components/SearchBar';

const MeetingCard = ({ meeting, isSelected, onSelect }) => (
  <motion.div
    layout
    initial={{ opacity: 0 }}
    animate={{ opacity: 1 }}
    exit={{ opacity: 0 }}
    className={`bg-gradient-to-br from-gray-800 to-gray-900 rounded-xl p-6 shadow-lg hover:shadow-xl transition duration-300 ${
      isSelected ? 'ring-2 ring-blue-500' : ''
    }`}
  >
    <h2 className="text-2xl font-semibold mb-4">{meeting.title}</h2>
    <div className="flex items-center text-gray-400 mb-2">
      <Calendar className="w-4 h-4 mr-2" />
      <span>{new Date(meeting.date).toLocaleDateString()}</span>
    </div>
    <div className="flex items-center text-gray-400 mb-4">
      <Users className="w-4 h-4 mr-2" />
      <span>{meeting.participants} </span>
    </div>
    <div className="flex justify-between items-center mb-4">
      <span className="px-3 py-1 bg-blue-500 text-white text-sm rounded-full">
        {meeting.type}
      </span>
      <span className="text-yellow-400 font-semibold">
        : {meeting.humanRightsRelevance}%
      </span>
    </div>
    <div className="flex justify-between items-center">
      <Link
        to={`/meetings/${meeting.id}`}
        className="text-blue-400 hover:text-blue-300 transition duration-300 flex items-center"
      >
        
        <ChevronRight size={16} className="ml-1" />
      </Link>
      <button
        onClick={() => onSelect(meeting.id)}
        className={`px-4 py-2 rounded-full transition duration-300 ${
          isSelected
            ? 'bg-blue-600 text-white'
            : 'bg-gray-700 text-gray-300 hover:bg-gray-600'
        }`}
      >
        {isSelected ? '' : ''}
      </button>
    </div>
  </motion.div>
);

const MeetingListPage = () => {
  const [meetings, setMeetings] = useState([]);
  const [filteredMeetings, setFilteredMeetings] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [selectedType, setSelectedType] = useState('');
  const [showFilters, setShowFilters] = useState(false);
  const [selectedMeetings, setSelectedMeetings] = useState([]);

  const meetingTypes = ['', '', '', '', ''];

  const mockMeetings = [
    {
      id: 1,
      title: "",
      date: "2024-09-01T09:00:00",
      participants: 50,
      type: "",
      humanRightsRelevance: 85
    },
    {
      id: 2,
      title: "",
      date: "2024-09-10T14:00:00",
      participants: 30,
      type: "",
      humanRightsRelevance: 60
    },
    {
      id: 3,
      title: "",
      date: "2024-09-15T13:30:00",
      participants: 25,
      type: "",
      humanRightsRelevance: 95
    },
    {
      id: 4,
      title: "",
      date: "2024-09-20T10:00:00",
      participants: 20,
      type: "",
      humanRightsRelevance: 40
    },
    {
      id: 5,
      title: "",
      date: "2024-09-25T09:00:00",
      participants: 35,
      type: "",
      humanRightsRelevance: 75
    },
    {
      id: 6,
      title: "",
      date: "2024-09-28T15:00:00",
      participants: 200,
      type: "",
      humanRightsRelevance: 50
    },
    {
      id: 7,
      title: "",
      date: "2024-10-05T10:00:00",
      participants: 40,
      type: "",
      humanRightsRelevance: 90
    },
    {
      id: 8,
      title: "",
      date: "2024-10-10T09:00:00",
      participants: 60,
      type: "",
      humanRightsRelevance: 70
    },
    {
      id: 9,
      title: "",
      date: "2024-10-15T14:00:00",
      participants: 20,
      type: "",
      humanRightsRelevance: 80
    },
    {
      id: 10,
      title: "",
      date: "2024-10-20T09:00:00",
      participants: 150,
      type: "",
      humanRightsRelevance: 65
    }
  ];

  useEffect(() => {
    fetchMeetings();
  }, []);

  const fetchMeetings = async () => {
    setLoading(true);
    try {
      //  API 
      await new Promise(resolve => setTimeout(resolve, 1000));
      setMeetings(mockMeetings);
      setFilteredMeetings(mockMeetings);
    } catch (error) {
      console.error("Error fetching meetings:", error);
      setError("");
    } finally {
      setLoading(false);
    }
  };

  const handleSearchResults = (results) => {
    setFilteredMeetings(results);
  };

  const handleFilterByType = (type) => {
    setSelectedType(type);
    if (type === '') {
      setFilteredMeetings(meetings);
    } else {
      setFilteredMeetings(meetings.filter(meeting => meeting.type === type));
    }
  };

  const handleMeetingSelection = (meetingId) => {
    setSelectedMeetings(prev => {
      if (prev.includes(meetingId)) {
        return prev.filter(id => id !== meetingId);
      } else if (prev.length < 3) {
        return [...prev, meetingId];
      }
      return prev;
    });
  };

  const renderContent = () => {
    if (loading) {
      return (
        <div className="flex items-center justify-center h-[calc(100vh-64px)]">
          <Loader className="animate-spin h-12 w-12 text-blue-400" />
        </div>
      );
    }

    if (error) {
      return (
        <div className="container mx-auto px-4 py-8">
          <div className="bg-red-100 border-l-4 border-red-500 text-red-700 p-4 rounded" role="alert">
            <p className="font-bold"></p>
            <p>{error}</p>
          </div>
        </div>
      );
    }

    return (
      <div className="container mx-auto px-4 py-8">
        <div className="flex flex-col md:flex-row justify-between items-center mb-8">
          <h1 className="text-4xl font-bold mb-4 md:mb-0"></h1>
          <button
            onClick={() => setShowFilters(!showFilters)}
            className="bg-blue-500 hover:bg-blue-600 text-white font-bold py-2 px-4 rounded-full transition duration-300 flex items-center"
          >
            <Filter size={18} className="mr-2" />
            
          </button>
        </div>

        <SearchBar 
          mockSuggestions={meetings.map(meeting => meeting.title)}
          mockResults={meetings}
          onResults={handleSearchResults}
        />

        {showFilters && (
          <div className="mb-8 p-4 bg-gray-800 rounded-lg shadow-lg">
            <h2 className="text-xl font-semibold mb-4"></h2>
            <div className="flex flex-wrap gap-2">
              {meetingTypes.map(type => (
                <button
                  key={type}
                  onClick={() => handleFilterByType(type)}
                  className={`px-4 py-2 rounded-full transition duration-300 ${
                    selectedType === type
                      ? 'bg-blue-500 text-white'
                      : 'bg-gray-700 text-gray-300 hover:bg-gray-600'
                  }`}
                >
                  {type}
                </button>
              ))}
            </div>
          </div>
        )}

        {selectedMeetings.length > 0 && (
          <div className="mb-8 p-4 bg-blue-900 rounded-lg shadow-lg">
            <div className="flex justify-between items-center">
              <span className="text-lg font-semibold"> {selectedMeetings.length} </span>
              <button
                onClick={() => console.log('', selectedMeetings)}
                className="bg-green-500 hover:bg-green-600 text-white font-bold py-2 px-4 rounded-full transition duration-300 flex items-center"
              >
                
              </button>
            </div>
          </div>
        )}

        {filteredMeetings.length === 0 ? (
          <div className="text-center py-12">
            <p className="text-2xl text-gray-400"></p>
            <p className="text-gray-500 mt-2"></p>
          </div>
        ) : (
          <motion.div layout className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
            <AnimatePresence>
              {filteredMeetings.map(meeting => (
                <MeetingCard
                  key={meeting.id}
                  meeting={meeting}
                  isSelected={selectedMeetings.includes(meeting.id)}
                  onSelect={handleMeetingSelection}
                />
              ))}
            </AnimatePresence>
          </motion.div>
        )}
      </div>
    );
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-900 via-gray-800 to-gray-900 text-white">
      <Navbar />
      {renderContent()}
    </div>
  );
};

export default MeetingListPage;
